<template>

<section>
  <validation-observer
      ref="observer"
      v-slot="{ invalid }"
  >
  <v-row no-gutters>
    <v-col
        cols="12"
        sm="6"
        md="6"
    >
      <v-card
          style="background: white; height: 100vh;"


      >
        <v-row no-gutters>
          <v-col style="background: white; height: 25vh;">
          </v-col>
        </v-row>
        <v-row no-gutters>
          <v-col cols="6 container" >
            <br>
            <h1>Iniciar sesión</h1>
            <br>
            <label style="font-weight: bold;">Correo electrónico :</label>
            <validation-observer
                ref="observer"
                v-slot="{ invalid }"
            >
              <form @submit.prevent="submit">
                <validation-provider
                    v-slot="{ errors }"
                    name="email"
                    rules="required|email"
                >
                  <v-text-field
                      v-model="email"
                      :error-messages="errors"
                      label="Ingrese correo electrónico"
                      clearable outlined dense
                      required
                  ></v-text-field>
                </validation-provider>
                <label style="font-weight: bold;">Contraseña :</label>
                <validation-provider
                    v-slot="{ errors }"
                    name="Password"
                    rules="required|min:10"
                >
                  <v-text-field
                      v-model="password"
                      type="Password"
                      :error-messages="errors"
                      label="Ingrese la contraseña"
                      clearable outlined dense
                      required
                  ></v-text-field>
                </validation-provider>

                <v-btn
                    class="mr-4"
                    type="submit"
                    x-large
                    color="#002F6C"
                    :to="'/menu'"
                    style="color: #E5F0F4; width: 100%"
                    :disabled="invalid"
                >
                  Iniciar sesión
                </v-btn>
              </form>
            </validation-observer>

          </v-col>
        </v-row>
        <v-row no-gutters>
        </v-row>


      </v-card>
    </v-col>
    <v-col
        cols="12 container"
        sm="6"
        md="6"
        fluid fill-height
    >
      <v-card
          style="background: #002F6C; height: 100vh;"


      >
        <v-row no-gutters>
          <v-col style="background: #002F6C; height: 25vh;">
          </v-col>
        </v-row>
        <v-row no-gutters>
          <v-col cols="6 container" >
            <v-img
                src="../assets/logo.png"
            ></v-img>
          </v-col>
        </v-row>
        <v-row no-gutters>
        </v-row>


      </v-card>
    </v-col>
  </v-row>
  </validation-observer>
</section>
</template>

<script>
import { required, digits, email, min, } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'

setInteractionMode('eager')

extend('required', {
  ...required,
  message: 'Campo no puede ir vacío',
})

extend('min', {
  ...min,
  message: 'La contraseña debe ser de mínimo {length} caracteres',
})


extend('email', {
  ...email,
  message: 'Correo electronico no es valido',
})

export default {
  name: "LoginComponent",
  components: {
    ValidationProvider,
    ValidationObserver,
  },
  data () {
    return {
      password: '',
      email: '',
    }
  },
  methods: {
    submit () {
      this.$refs.observer.validate()
    },
  }
}
</script>

<style lang="scss" scoped>
</style>